export default function IsUrl(path: string): boolean { // tslint:disable-line:export-name
  /* Matches the following:
   * http://contoso.org/foo.js
   * https://contoso
   * http:/\foo.bar
   * https:\/foo.bar
   * http:\\foo.bar
   * https:\\foo.bar
   * HTTp:\\foo.bar
   */
  const urlRegex: RegExp = /([hH][tT]{2}[pP]s?):[/\\]{2}/;

  return !!path.match(urlRegex);
}
